@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;700&display=swap);
:root {
  /* Light Theme */
  --clr-bg: #e3fdfd;
  --clr-fonts: #0a666b;
  --clr-borders: #71c9ce;
  --clr-hoverFont: #fdfdfd;
  --clr-hover: #56dbe8;
  --clr-buttons: #a6e3e9;
  --clr-highlight: #ff33a3;
  --clr-inputBg: #ffffff;
  --clr-themeIcon: #ffa600;
}

[data-theme="dark"] {
  --clr-bg: #334257;
  --clr-fonts: #eeeeee;
  --clr-borders: #548ca8;
  --clr-hoverFont: #000000;
  --clr-hover: #e4b715;
  --clr-buttons: #476072;
  --clr-highlight: #ff33a3;
  --clr-inputBg: #3a3a3a;
  --clr-themeIcon: #e7eb0e;
}

/* Font Sizes */

/* Font Weights */

/* Timers */

/* Border Radius */

* {
  box-sizing: border-box;
}

body {
  margin: auto;
  max-width: 56em;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--clr-bg);
  color: var(--clr-fonts);
  font-weight: 300;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  border: none;
  padding: 0;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

input {
  border: none;
  padding: 0;
  outline: none;
}

